import React from 'react'
// import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';

import './AboutUs.css';

import imgRoxana from '../../assets/images/aboutUs/roxana.jpg';
import imgAnna from '../../assets/images/aboutUs/anna.jpg';
import imgMelanieFay from '../../assets/images/aboutUs/melanieFay.jpg';

const imageStyles = { width: '150px', height: 'auto' };

const AboutUs = () => (
  <div id='AboutUs'>
    <Card style={ { display: 'flex', margin: '10px', alignItems: 'center'} }>
      <CardMedia
        className='img imgTeaching'
      >
        <img src={ imgRoxana } style={imageStyles} alt='Roxana Nez Trabucco' />
      </CardMedia>
      <CardContent>
        <Typography gutterBottom variant="h5" component="h2">
          Roxana Nez Trabucco - Author
        </Typography>
        <Typography component="p">
          Roxana is a Peruvian author that holds a Masters Degree in Applied Linguistics to Teach Spanish as a Foreign Language, from the University of Jan (Spain). Throughout her more than 16 years of experience as an elementary school Spanish teacher, and before beginning her career as an author, Roxana developed material considering her students suggestions and the specific conditions where she had to teach Spanish, as an after school activity.
        </Typography>
        <Typography component="p">
          The results are these textbooks series Have Fun Learning Spanish that have been tested several times and try to expose children to the Spanish language at a young age, emphasizing the importance of movement and playing while learning, and at the same time introducing children to literacy and reading as a base to acquire a solid foundation in the Spanish language.
        </Typography>
        <Typography component="p">
          Roxanas works is in process as she is still developing new textbooks, songs to reinforce what has been learned, bilingual stories and online material for teachers and students.
        </Typography>
        <Typography component="p">
          In her free time Roxana enjoys traveling, reading, and translating childrens and young adults books into Spanish.
        </Typography>
      </CardContent>
    </Card>

    <Card style={ { display: 'flex', margin: '10px'  } }>
      <CardMedia
        className='img imgTeaching'
      >
        <img src={ imgAnna } style={imageStyles} alt='Roxana Nez Trabucco' />
      </CardMedia>
      <CardContent>
        <Typography gutterBottom variant="h5" component="h2">
          Anna Alexeeva - Illustrator
        </Typography>
        <Typography component="p">
          Anna Alexeeva is a freelance artist living in the East Bay area of California since 2010. She was born in Minsk, Belarus.
        </Typography>
        <Typography component="p">
          Annas main focus is graphic design and illustration, but she also has a passion for fine arts. She loves to experiment with any medium that can bring her ideas to life, from digital pen to oil.
        </Typography>
        <Typography component="p">
          Hanna loves to study the world and view things from different perspectives, finding something special in everyday life and enjoying simple things. In her free time, Anna enjoys reading and spending time with her family. Some of her hobbies include painting, camping, and kitesurfing.
        </Typography>
      </CardContent>
    </Card>


    <Card style={ { display: 'flex', margin: '10px'  } }>
      <CardMedia
        className='img imgTeaching'
      >
        <img src={ imgMelanieFay } style={imageStyles} alt='Roxana Nez Trabucco' />
      </CardMedia>
      <CardContent>
        <Typography gutterBottom variant="h5" component="h2">
          Melanie Fay - Illustrator
        </Typography>
        <Typography component="p">
          Melanie Fay is a freelance illustrator and art teacher living in upstate New York. She studied at The School of the Art Institute of Chicago and holds a BFA from The Cooper Union School of Art. She loves animals and art projects and really anything that is creative and fun.
        </Typography>
      </CardContent>
    </Card>
  </div>
)

export default AboutUs


